<template>
  <div class="card" style="height: 95%;">
    <div class="card__title">
      <b-row>
        <b-col style="max-width:max-content !important;">
          <span>Fleet Master Status</span>
        </b-col>
      </b-row>
    </div>
    <div class="card__body">
      <b-row>
        <b-col style="max-width: max-content !important;">
          <span @click="changeProspect('min')" style="cursor: pointer;">
            <font-awesome-icon class="isGrey" icon="less-than" size="lg" />
          </span>
        </b-col>
        <b-col style="text-align: center">
          <span>
            <h6 class="isGrey">{{ DataProspect.monthName + ' ' + DataProspect.year }}</h6>
          </span>
        </b-col>
        <b-col style="text-align: right;max-width: max-content !important;">
          <span @click="changeProspect('add')" style="cursor: pointer;">
            <font-awesome-icon class="isGrey" icon="greater-than" size="lg" />
          </span>
        </b-col>
      </b-row>
      <b-row class="row-dash-new-prospect-top" style="margin-top: 70px;">
        <b-col>
          <div class="buleth__blue">
            <span>{{ DataProspect.applicable}}</span>
          </div>
          <div class="buleth-text" style="margin-top: 20px !important;">Applicable</div>
        </b-col>
        <b-col>
          <div class="buleth__red">
            <span>{{ DataProspect.expire }}</span>
          </div>
          <div class="buleth-text" style="margin-top: 20px !important;">Expire</div>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      DataProspect: {
        month: "",
        monthName: "",
        year: "",
        applicable: 0,
        expire: 0
      }
    };
  },
  methods: {
    getProspect(date = null) {
      var now = date ? new Date(date) : new Date();
      var month = now.getMonth();
      var year = now.getFullYear();

      this.DataProspect = {
        month: month + 1,
        monthName: this.getMonthName(now),
        year: year,
        applicable: 0,
        expire: 0
      };

      var param = {
        option_function_cd: "GetFleetStatusDash",
        module_cd: "SP",
        ss_portfolio_id: this.getDataUser().portfolio_id,
        user_id: this.getDataUser().user_id,
        years: year,
        months: month + 1
      };
      this.CallFunction(param).then(ress => {
        if (ress == null) return;

        if (ress.Data.length > 0) {
          var data = ress.Data[0];

          this.DataProspect = {
            month: month + 1,
            monthName: this.getMonthName(now),
            year: year,
            applicable: data.applicable,
            expire: data.stnk_expiry
          };
        } else {
          this.DataProspect = {
            month: month + 1,
            monthName: this.getMonthName(now),
            year: year,
            applicable: 0,
            expire: 0
          };
        }
      });
    },
    changeProspect(act) {
      var date = new Date();
      if (act === "min") {
        let mindate = this.momentDateFormat(this.dateAdd2("m", date, -12), 'YYYY-MM-01');
        var mth = this.DataProspect.month && this.DataProspect.month !== "" ? parseInt(this.DataProspect.month) : 0;
        var year = this.DataProspect.year;
        mth = mth < 10 ? "0" + mth : mth;
        var frm = year + "-" + mth + "-" + "01";
        date = this.dateAdd2("m", frm, -1);
          // console.log(new Date(date), mindate)
          // console.log(new Date(date) < mindate)
          // console.log(new Date(date) < new Date(mindate))
        if (new Date(date) < new Date(mindate)) {
          // console.log('xx')
          return
        }
      } else if (act === "add") {
        let maxdate = this.momentDateFormat(this.dateAdd2("m", date, 12), 'YYYY-MM-01');
        var mth = this.DataProspect.month && this.DataProspect.month !== "" ? parseInt(this.DataProspect.month) : 0;
        var year = this.DataProspect.year;
        mth = mth < 10 ? "0" + mth : mth;
        var frm = year + "-" + mth + "-" + "01";
        date = this.dateAdd2("m", frm, 1);
        if (new Date(maxdate) < new Date(date)) {
          // console.log('xx')
          return
        }
      }

      this.getProspect(date);
    }
  },
  mounted() {
    this.getProspect();
  }
};
</script>